.headerWrapper {
  max-width: calc(var(--container-width) + var(--spacing-unit) * 2);
  margin: 0 auto;
  height: var(--header-wrapper-height);
}

.headerContainer {
  justify-content: space-between;
  align-items: center;
  height: var(--header-wrapper-height);
}

.headerLogo {
  line-height: 1.2;
  align-items: center;
  font-size: var(--font-size-b);
  padding: var(--spacing-unit-xmicro) 0;
}

.logoImg {
  flex-shrink: 0;
  height: 1.5em;
}

.logoImg img {
  height: inherit;
  width: auto;
}

.logoText {
  margin-left: 0.6em;
  font-weight: 500;
}

.onlineText {
  margin-left: 0.2em;
  display: flow-root;
  font-variation-settings: "wdth" 85;
}

.logoText:before {
  content: "";
  display: block;
  height: 0;
  width: 0;
  margin-top: -0.2075em;
}

.logoText:after {
  content: "";
  display: block;
  height: 0;
  width: 0;
  margin-bottom: -0.2075em;
}

.dropShadow {
  box-shadow: var(--drop-shadow);
}

@media screen and (max-width: 860px) {
  .onlineText {
    display: none;
  }

  .logoImg {
    flex-shrink: 0;
    height: 1.1em;
  }

  .headerLogo {
    margin-left: 25px;
  }
}

.chatBox {
  height: calc(100vh - 120px);
  overflow: auto;
  display: flex;
  flex-direction: column-reverse;
  padding-bottom: 10px;
}

@media screen and (max-width: 860px) {
  .chatBox {
    height: calc(50vh - 80px);
  }
}

.messageContainer {
  width: 100%;
  display: flex;
}

.message {
  font-size: var(--font-size-b);
  border-radius: 5px;
  border-width: 2px;
  border-style: solid;
  padding: 2px 5px 2px 5px;
  margin: 6px 6px 0px 6px;
  word-wrap: break-word;
  max-width: 50%;
}

.yourMessage {
  background-color: var(--primary-color-background);
  border-color: var(--primary-color);
  margin-left: auto;
}

.partnerMessage {
  background-color: var(--secondary-color-background);
  border-color: var(--secondary-color);
}

.systemMessage {
  background-color: gray;
  border-color: black;
}

