@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500;600;700&display=swap");
@import "../src/Components/Navigation/Navigation.css";

:root {
  --primary-color: #26c6da;
  --primary-color-background: #3bcbdd;
  --primary-color-hover: rgba(13, 163, 182, 0.08);
  --primary-color-active: rgba(13, 163, 182, 0.16);
  --secondary-color: #ffda68;
  --secondary-color-background: #ffda68;
  --secondary-color-hover: #ffe082;
  --secondary-color-active: #ffecb3;
  --warning-color: #f44336;
  --warning-color-hover: #ffcdd2;
  --warning-color-active: #ef9a9a;
  --drop-shadow: 0 1px 6px rgba(0, 0, 0, 0.1), 0 4px 24px rgba(0, 0, 0, 0.06);
  --lift-shadow: 0 1px 4px rgba(0, 0, 0, 0.1), 0 4px 12px rgba(0, 0, 0, 0.06);
  --color-text: 1, 41, 46;
  --color-background: 255, 255, 255;
  --color-dark-background: 25, 33, 36;
  --grey-1: rgb(120, 144, 156);
  --color-input-border: 228, 232, 235;
  --container-width: 60rem;
  --rounded-radius: var(--spacing-unit-xmicro);
  --site-margin: var(--spacing-unit);
  --spacing-unit-xxmicro: 3px;
  --spacing-unit-xmicro: 0.25rem;
  --spacing-unit-micro: 0.3535534rem;
  --spacing-unit-tiny: 0.5000000266rem;
  --spacing-unit-small: 0.7071068376rem;
  --spacing-unit: 1.0000001064rem;
  --spacing-unit-large: 1.4142137505rem;
  --spacing-unit-xlarge: 2.0000003193rem;
  --spacing-unit-xxlarge: 2.8284276515rem;
  --spacing-unit-xxxlarge: 4.0000008514rem;
  --spacing-unit-xxxxlarge: 5.6568556041rem;
  --font-weight-regular: 400;
  --font-weight-text: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-size-a: 4rem;
  --font-size-b: 2rem;
  --font-size-c: 1rem;

  --main-container-margin-left: 580px;
  --main-container-margin-top: 0;

  --video-container-width: 580px;
  --video-container-height: calc(50% - 35px);
  --video-container-position: absolute;
  --video-container-z-index: 99;
  --video-container-background-color: rgba(0, 0, 0, 1);

  --user-video-container-top: calc(50% + 35px);
  --user-video-container-left: 0;
  --user-video-container-width: 580px;
  --user-video-container-height: calc(50% - 35px);

  --partner-video-container-top: 70px;

  --video-width: 100%;
  --video-height: 100%;
  --object-fit: fill;

  --controls-container-position: absolute;
  --controls-container-top: calc(100vh - 70px);
  --controls-container-align-items: center;
  --controls-container-justify-content: space-around;
  --controls-container-height: 70px;
  --controls-container-z-index: 999;
  --controls-container-width: 580px;

  --icon-container-padding: 10px;
  --icon-container-border-radius: 50%;
  --icon-container-border-width: 2px;
  --icon-container-border-style: solid;
  --icon-container-width: 1.25rem;
  --icon-container-height: 1.25rem;
  --icon-container-cursor: pointer;
  --icon-container-transition: background-color 0.2s
    cubic-bezier(0.4, 0, 0.2, 1);

  --header-wrapper-height: 70px;

  --input-container-height: 40px;
}

@media screen and (min-width: 861px) {
}

@media screen and (max-width: 860px) {
  :root {
    --font-size-a: 2rem;
    --font-size-b: 1rem;
    --font-size-c: 1rem;

    --main-container-margin-left: 0px;
    --main-container-margin-top: 50vh;

    --video-container-width: 100%;
    --video-container-height: 50vh;

    --user-video-container-top: 50px;
    --user-video-container-right: 20px;
    --user-video-container-width: 30vw;
    --user-video-container-height: 15vh;
    --user-video-container-z-index: 100;

    --partner-video-container-top: 30px;

    --controls-container-top: 45vh;
    --controls-container-z-index: 999;
    --controls-container-width: 100%;

    --icon-container-padding: 10px;
    --icon-container-cursor: none;

    --header-wrapper-height: 30px;

    --input-container-height: 40px;
  }
}

@media screen and (max-width: 60rem) and (min-width: 20rem) {
  .form-input {
    font-size: calc(1.1375rem + 0.08125 * (100vw - 20rem) / 40);
  }
}

body {
  margin: 0;
  font-family: "IBM Plex Sans", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
}

.flex {
  display: flex;
}

.flex-row {
  flex-direction: row;
}

.flex-column {
  flex-direction: column;
}

.flex-center {
  align-items: center;
  justify-content: center;
}

.welcomeText {
  font-size: var(--font-size-a);
  letter-spacing: -0.0075em;
  line-height: 1.1;
  font-weight: var(--font-weight-semibold);
  margin-top: var(--spacing-unit-xxxxlarge);
  text-align: center;
}

.descriptionText {
  font-size: var(--font-size-b);
  line-height: 1.35;
  font-weight: var(--font-weight-text);
  letter-spacing: -0.0075em;
  margin-bottom: var(--spacing-unit-large);
  text-align: center;
}

.primaryButton {
  border: none;
  outline: none;
  cursor: pointer;
  font-size: var(--font-size-c);
  font-weight: var(--font-weight-semibold);
  line-height: 1.25;
  padding: var(--spacing-unit-small);
  color: #1f292e;
  border-radius: 0.25rem;
  border-radius: var(--rounded-radius);
}

.primaryButton:hover,
.primaryButton:active {
  box-shadow: var(--lift-shadow);
}

.next {
  background-color: var(--secondary-color);
  transition: background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1),
    color 0.2s cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.cancel {
  background-color: var(--warning-color);
  transition: background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1),
    color 0.2s cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.next:hover {
  background-color: var(--secondary-color-hover);
}

.next:active {
  background-color: var(--secondary-color-active);
}

.cancel:hover {
  background-color: var(--warning-color-hover);
}

.cancel:active {
  background-color: var(--warning-color-active);
}

.mainContainer {
  margin-left: var(--main-container-margin-left);
  margin-top: var(--main-container-margin-top);
}

.videoContainer {
  width: var(--video-container-width);
  position: var(--video-container-position);
  z-index: var(--video-container-z-index);
  background-color: var(--video-container-background-color);
  height: var(--video-container-height);
}

.userVideoContainer {
  top: var(--user-video-container-top);
  right: var(--user-video-container-right);
  width: var(--user-video-container-width);
  height: var(--user-video-container-height);
  z-index: var(--user-video-container-z-index);
}

.partnerVideoContainer {
  top: var(--partner-video-container-top);
}

.video {
  width: var(--video-width);
  height: var(--video-height);
  object-fit: var(--object-fit);
  box-shadow: var(--lift-shadow);
}

.userVideo {
  /* temp */
}

.partnerVideo {
  /* temp */
}

.controlsContainer {
  position: var(--controls-container-position);
  top: var(--controls-container-top);
  align-items: var(--controls-container-align-items);
  justify-content: var(--controls-container-justify-content);
  height: var(--controls-container-height);
  z-index: var(--controls-container-z-index);
  width: var(--controls-container-width);
}

.iconContainer {
  padding: var(--icon-container-padding);
  border-radius: var(--icon-container-border-radius);
  border-width: var(--icon-container-border-width);
  border-style: var(--icon-container-border-style);
  border-color: var(--primary-color);
  width: var(--icon-container-width);
  height: var(--icon-container-height);
  cursor: var(--icon-container-cursor);
  background-color: var(--primary-color-hover);
  box-shadow: var(--lift-shadow);
  transition: var(--icon-container-transition);
}

.iconContainer:hover {
  background-color: rgba(var(--color-background), 0.2);
}

.iconContainer img {
  width: 100%;
  height: auto;
}

.chatContainer {
}

.inputContainer {
  bottom: 0;
  width: 100%;
  z-index: 99;
  height: var(--input-container-height);
}

.chatInput {
  width: calc(100% - 96px);
  border: 3px solid var(--primary-color);
  padding: 11px 32px 8px 8px;
  font-size: 0.8em;
  outline: none;
}

.attachmentButton {
  position: absolute;
  right: 60px;
  z-index: 4;
  margin-top: 12px;
  font-size: 1.1em;
  color: var(--primary-color);
  opacity: 0.5;
  cursor: pointer;
}

.chatButton {
  border: none;
  width: 50px;
  padding: 13px 0 11px 0;
  cursor: pointer;
  background: var(--primary-color);
  color: #fff;

  &:hover {
    background: var(--primary-color-background);
  }
  outline: none;
}
